%margin {
    margin: 0 auto;
}

%marginauto {
    margin: auto;
}

%d-flex {
    display: flex;
}

%t-a-center {
    text-align: center;
}

%d-i-block {
    display: inline-block;
}

%list-s-none {
    list-style: none;
}

%color-gradient {
    background: -webkit-linear-gradient($experience_tabs_hover);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

%carousel-indicators {
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background-color: $blue;
}

%padding-bottom-main {
    padding-bottom: 40px;
}

%border-bg-wrapper {
    border-radius: 0 0 12px 12px;
}

%border-radius-main {
    border-radius: 12px;
}

%border-grey {
    box-shadow: 0 4px 0 0 $experience_tabs_border;
}

%border-top-for-footer {
    &::after {
        @extend %border-grey;
        content: "";
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 4px;
        z-index: 1;
    }
}

%transition-main {
    transition: .3s;
}

%dot-size {
    width: 6px;
    height: 6px;
}